:root {
  font-size: 62.5%;
}

*, :before, :after {
  box-sizing: border-box;
  font: inherit;
  margin: 0;
  padding: 0;
}

img, picture, svg, video {
  max-width: 100%;
  display: block;
}

body {
  min-height: 95vh;
  background-color: #fdfdfd;
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

.header {
  height: 10rem;
  width: 100%;
  z-index: 100;
  background-color: #fdfdfd;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-top: 1rem;
  padding-bottom: .8rem;
  display: flex;
  position: fixed;
  top: 0;
}

@media screen and (min-width: 501px) {
  .header {
    height: 6rem;
    flex-direction: row;
    padding-top: 1rem;
    padding-left: 1rem;
    padding-right: 1rem;
  }
}

@media screen and (min-width: 640px) {
  .header {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

.navbar ul {
  width: -moz-fit-content;
  width: fit-content;
  align-items: center;
  font-size: 1.8rem;
  font-weight: 500;
  list-style-type: none;
  display: flex;
}

.navbar a {
  color: #000;
  letter-spacing: 1px;
  border-radius: 2rem;
  margin: 0 1rem;
  padding: .4rem .9rem;
  text-decoration: none;
}

.navbar a:hover {
  color: #fdfdfd;
  background-color: #000;
  border-radius: 2rem;
  transition-duration: .5s;
}

.navbar a:active {
  text-decoration: underline;
}

.logo {
  height: 5rem;
  width: 10rem;
  background-image: url("logo05.6d9f5814.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

.heading {
  text-align: center;
  position: relative;
  top: 15rem;
}

@media screen and (min-width: 501px) {
  .heading {
    top: 10rem;
  }
}

.heading h1 {
  letter-spacing: 1px;
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: 500;
}

@media screen and (min-width: 768px) {
  .heading h1 {
    font-size: 2.2rem;
  }
}

@media screen and (min-width: 1089px) {
  .heading h1 {
    font-size: 2.4rem;
  }
}

.heading p {
  color: #484848;
  margin-top: 1rem;
  font-size: 1.2rem;
}

@media screen and (min-width: 768px) {
  .heading p {
    font-size: 1.3rem;
  }
}

@media screen and (min-width: 1089px) {
  .heading p {
    font-size: 1.5rem;
  }
}

.card-space {
  min-height: 80vh;
  width: 90%;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  gap: 4rem;
  margin: 20rem auto 5rem;
  display: flex;
}

@media screen and (min-width: 501px) {
  .card-space {
    margin-top: 14rem;
  }
}

@media screen and (min-width: 768px) {
  .card-space {
    flex-flow: wrap;
  }
}

@media screen and (min-width: 980px) {
  .card-space {
    align-items: flex-start;
  }
}

.card {
  height: 40rem;
  width: 30rem;
  max-width: 36rem;
  perspective: 100rem;
  transform-style: preserve-3d;
  transition-duration: 1.5s;
  position: relative;
}

.card:hover .card-front {
  transform: rotateY(-180deg);
}

.card:hover .card-back {
  visibility: visible;
  transform: rotateY(0deg);
}

@media screen and (min-width: 1200px) {
  .card {
    flex-grow: 1;
  }
}

.card-front {
  height: 100%;
  width: 100%;
  backface-visibility: hidden;
  background-color: #fff;
  border: 1px solid #a1a1a1;
  border-radius: 1rem;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  transition-duration: 1.5s;
  display: flex;
  position: absolute;
  transform: rotateY(0deg);
  box-shadow: 0 4px 8px #0003;
}

.card-front .preview-pic {
  width: 25rem;
  height: 25rem;
  background-image: url("https://placeimg.com/250/150/tech");
}

.card-front h2 {
  text-align: center;
  padding-bottom: 2rem;
  font-size: 1.8rem;
  font-weight: 500;
}

.card-front-1 .preview-pic {
  background-image: url("design01.c80dbebe.jpg");
  background-size: cover;
}

.card-front-2 .preview-pic {
  background-image: url("neumorph.3c1ddf83.jpg");
  background-size: cover;
}

.card-front-3 .preview-pic {
  background-image: url("gallery02.3fbc8821.jpg");
  background-position: center;
  background-size: cover;
}

.card-front-4 .preview-pic {
  background-image: url("speaker01.7ab264ca.jpg");
  background-position: center;
  background-size: cover;
}

.card-front-5 .preview-pic {
  background-image: url("tech01.c8e02659.jpg");
  background-position: center;
  background-size: cover;
}

.card-front-6 .preview-pic {
  background-image: url("coffee01.90624209.jpg");
  background-position: center;
  background-size: cover;
}

.card-back {
  height: 100%;
  width: 100%;
  backface-visibility: hidden;
  visibility: hidden;
  border: 1px solid #a1a1a1;
  border-radius: 1rem;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  transition-duration: 1.5s;
  display: flex;
  position: absolute;
  transform: rotateY(180deg);
  box-shadow: 0 4px 8px #0003;
}

.card-back h2 {
  text-align: center;
  color: #484848;
  font-size: 1.8rem;
  font-weight: 500;
}

.card-back p {
  width: 80%;
  text-align: center;
  font-size: 1.4rem;
}

.card-back .more-info p {
  color: #484848;
  font-size: 1.3rem;
}

.card-back .more-info {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.card-back .more-info a {
  color: #000;
  border: 1px solid #000;
  border-radius: 2rem;
  margin-top: .8rem;
  padding: .4rem .9rem;
  font-size: 1.4rem;
  text-decoration: none;
}

.card-back .more-info a:hover {
  color: #fdfdfd;
  background-color: #000;
  border-radius: 2rem;
  text-decoration: none;
  transition-duration: .5s;
}

.card-back .more-info i {
  margin-right: .5rem;
}

